import { Component, OnInit, OnDestroy } from '@angular/core';
import { Headers, Http } from '@angular/http';
import { UserService } from '../../../service/user.service';

@Component({
  selector: 'app-article',
  templateUrl: './article.component.html',
  styleUrls: ['./article.component.scss']
})
export class ArticleComponent implements OnInit,OnDestroy {

  data:any = [];
  eventSub;
  httpSub;

  constructor( public http:Http, public userService: UserService ) {
    this.userService.scrollToTop.emit();
    this.getdata();
    this.eventSub = this.userService.hot_article_get_data.subscribe(() => {
      this.getdata();
    });
  }

  //获取数据
  getdata() {
    
    let url = "https://www.devonhello.com/chihu2/hot_answer";

    var headers = new Headers();
    headers.append('Content-Type', 'application/x-www-form-urlencoded');

    this.httpSub = this.http.post(url, "type=0" + "&len=" + this.data.length, {
      headers: headers
    })
      .subscribe((res) => {
        this.data = this.data.concat(res.json());
      });
  }

  ngOnDestroy() {
    this.eventSub.unsubscribe();
    this.httpSub.unsubscribe();
  }

  ngOnInit() {
  }

}



